import React from 'react';
import { getDashboard } from '../../client/awsClient';
import { isAWSTokenValid } from '../../client/awsLogic';
import {useStore, useDispatch} from '../../state/action-store';

const Dashboard = () => {
    const {user, token} = useStore();
    const dispatch = useDispatch();

    const getData = () => {
        const isValidToken = isAWSTokenValid(token);
        console.log('and the validation says token valid is ', isValidToken);
        if(!isValidToken) {
            dispatch(getDashboard(dispatch, user.getAuthResponse().id_token, token));
        }
    }

    return (
        <div>
            <div>Dashboard</div>
            <button onClick={getData}>Get Data</button>
        </div>
    )
}

export default Dashboard;